@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.section-card {
    position:relative;
    width: 100%;
    display:flex;
    flex-direction:row;

    /*background:darken(rgb(2, 117, 121), 10%);*/
    background:white;
    color:rgb(0, 0, 0);
    margin: 0 0 ;
    z-index:99;
    /*padding: 7vh 0;*/
    @include mobile-only {
        flex-wrap:wrap;
        padding-bottom: 8vh;
        position:relative;
    }

        &:nth-child(even) {
        color:darken(rgb(3, 152, 158), 1%);
        flex-direction: row-reverse;
        background:rgb(251, 251, 251);
        }

    &__image {
        width: 32vw;
        height: 40vw;
        object-fit:cover;
        filter: saturate(0.9);
        
        &:hover {
            filter: saturate(1.2);
        }
        @include mobile-only {
            width:100%;
            height: 90vw;
            margin-top:-5vw;
            clip-path: polygon(0 0, 100% 5%, 100% 100%, 0 95%);
        }
    }
    &__text-container {
        padding: 1vw;
        height: 20vh;
        width: 65vw;
        display:flex;
        flex-direction:row;
        flex-wrap:wrap;
        align-items:flex-start;
        align-content:flex-start;
        justify-content:center;
        position:relative;
        @include mobile-only {
            height: max-content;
            width:99vw;
            padding:0;

        }
        & > * {
            width:100%;
        }
        
        & i {
            font-family: 'Font Awesome 5 Free';
            font-weight: 600;
            border-radius: 50%;
            font-size: 3vw;
            width: 33%;
            height:3vw;
            display:flex;
            align-items:center;
            justify-content:center;
            margin-bottom: 1vw;
            padding:0;
            transition: 1s ease-in-out;
            @include mobile-only {
                font-size:10vw;
                height: 10vw;
                margin: 5vh 0;
            }
            &:hover {
                transform:scale(1.1);
            }
        }
    }
    &__title {
        font-weight: 200;
        font-size: 4vw;
        margin: 0;
        position:absolute;
        top: 1vh;
        z-index:99;

        width:max-content;
        color: white;
        text-shadow: 2px 2px 2px black;
        border-bottom: 0.3vw solid rgba(255, 255, 255, 0.637);
        border-radius: 1vw;
        padding:0.3vw;
        margin: 0 5vh;
        font-family: 'Dancing Script';
        @include mobile-only {
            border-bottom: 1vw solid rgb(255, 255, 255);
            padding:0;
            margin:0;
            font-size: 9vw;
            top:5vw;
            box-shadow: unset;
        }

    }
    &__subtitle {
        font-size: 2vw;
        line-height: 2vw;
        display:flex;
        flex-direction:row;
        justify-content:space-evenly;
        align-items:center;
        text-align:center;
        margin-top: 2vw;
        font-weight: normal;
        font-family: 'Dancing Script';
        @include mobile-only {
            font-size:3.5vh;
            line-height:4vh;
            position:absolute;
            top: -12vh;
            color: rgb(0, 87, 109);
            background:rgba(255, 255, 255, 0.746);
            padding: 1vw;

        }
    }
    &__content {
        height: 30vw;
        display:flex;
        flex-direction:row;
        flex-wrap:wrap;
        @include mobile-only {
            height: max-content;
            width: 100%;

        }

        & > * {
            margin: 1vw 0;
            font-size: 1.2vw;
            font-weight: 400;
            width: 63vw;
            text-align:justify;
            color:rgb(0, 89, 131);
            &:nth-child(odd) {
                color: rgb(58, 58, 58);
            }
            @include mobile-only {
                width:80%;
                padding-left: 5vw;
                font-size: 2vh;
            }
        }
    }
}